function referenceCarte(item) {
    const element = item["TASK REFERENCE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "").trim();
}

function typeCarte(item) {
    return "MC";
}

function specialite(item) {
    return item["SKILL"].replaceAll(/\r|\n/g, "");
}

function controle(item) {
    if(item["SOURCES"].toLowerCase().includes("cmr") || item["SOURCES"].toLowerCase().includes("awl")) {
        return "I";
    } else {
        return "N";
    }
}

function report(item) {
    if(item["SOURCES"].toLowerCase().includes("cmr") || item["SOURCES"].toLowerCase().includes("awl")) {
        return "N";
    } else {
        return "Y";
    }
}

function outOfPhase(codeCheck) {
    if(codeCheck === "to be filled") {
        return codeCheck;
    } else if(codeCheck.trim().length === 0) {
        return "Y";
    } else {
        return "N"
    }
}

function categorieMRB(item) {
    const value = item["SOURCES"];
    if(value) {
        if(value.includes("CMR")) {
            return "CMR";
        } if(value.includes("AWL")) {
            return "AWL";
        } if(value.includes("AD")) {
            return "AD";
        } if(value.includes("MRBR")) {
            return "MRBR";
        } if(value.includes("LUR")) {
            return "LUR";
        } if(value.includes("CPCP")) {
            return "CPCP";
        } if(value.trim().length === 0) {
            return "";
        }
        return value.split(":")[0].replaceAll(/[\(\)]/g, "").trim();
    }
    return "";
}

function codeTache(item) {
    return "to be filled";
}

function seuil1(item) {
    const result = {
        code: "",
        value: ""
    };
    const value =  item["THRESHOLD/INTERVAL/SAMPLE"].replaceAll(/\r/g, "");
    if(value) {
        if(value.includes("T:") || value.includes("T :")) {
            const values = value.split("\n");
            const filter = values.filter(x => (x.includes("T:") || x.includes("T :")) && x.includes("FH"))[0];
            if(filter && filter.trim().length > 0) {
                result.code = "FH"; //filter.split(":")[1].trim().split(" ")[1].trim();
                result.value = filter.split(":")[1].trim().split(" ")[0].trim();
            }
        }
    }
    return result;
}

function seuil2(item) {
    const result = {
        code: "",
        value: ""
    };
    const value =  item["THRESHOLD/INTERVAL/SAMPLE"].replaceAll(/\r/g, "");
    if(value) {
        if(value.includes("T:") || value.includes("T :")) {
            const values = value.split("\n");
            const filter = values.filter(x => (x.includes("T:") || x.includes("T :")) && (x.includes("FC") || x.includes("LD")))[0];
            if(filter && filter.trim().length > 0) {
                result.code = "FC"; //filter.split(":")[1].trim().split(" ")[1].trim();
                result.value = filter.split(":")[1].trim().split(" ")[0].trim();
            }
        }
    }
    return result;
}

function seuil3(item) {
    const calendarCodes = [
        "YE", "MO", "DY"
    ];
    const result = {
        code: "",
        value: ""
    };
    const value =  item["THRESHOLD/INTERVAL/SAMPLE"].replaceAll(/\r/g, "");
    if(value) {
        if(value.includes("T:") || value.includes("T :")) {
            const values = value.split("\n");
            const filter = values.filter(x => (x.includes("T:") || x.includes("T :")) &&
            (calendarCodes.map(c => x.includes(c)).filter(f => f == true).length > 0))[0];
            if(filter && filter.trim().length > 0) {
                result.code = filter.split(":")[1].trim().split(" ")[1].trim();
                result.value = filter.split(":")[1].trim().split(" ")[0].trim();
            }
        }
    }
    return result;
}

function heures(item) {
    const CtoFH = 8000;
    const AtoFH = 750;
    let result = "";

    const value =  item["THRESHOLD/INTERVAL/SAMPLE"].replaceAll(/\r/g, "");
    if(value) {
        if(value.includes("I:") || value.includes("I :")) {
            const values = value.split("\n");
            const filter = values.filter(x => (x.includes("I:") || x.includes("I :")) && x.includes("FH"))[0];
            if(filter && filter.trim().length > 0) {
                result = filter.split(":")[1].trim().split(" ")[0].trim();
            }

            const codeCfilter = values.filter(x => (x.includes("I:") || x.includes("I :")) && x.includes(" C"))[0];
            if(codeCfilter && codeCfilter.trim().length > 0) {
                const cValue = codeCfilter.split("I: ").filter(x => x.length > 0)[0].split(" ").filter(x => x.length > 0)[0];
                if(cValue == "C") {
                    result = 1 * CtoFH;
                } else if(!isNaN(Number(cValue))) {
                    result = Number(cValue) * CtoFH;
                }
            }

            const codeAfilter = values.filter(x => (x.includes("I:") || x.includes("I :")) && x.includes(" A"))[0];
            if(codeAfilter && codeAfilter.trim().length > 0) {
                const aValue = codeAfilter.split("I: ").filter(x => x.length > 0)[0].split(" ").filter(x => x.length > 0)[0];
                if(aValue == "A") {
                    result = 1 * AtoFH;
                } else if(!isNaN(Number(aValue))) {
                    result = Number(aValue) * AtoFH;
                }
            }
        }
    }

    return String(result);
}

function cycles(item) {
    let result = "";
    const value =  item["THRESHOLD/INTERVAL/SAMPLE"].replaceAll(/\r/g, "");
    if(value) {
        if(value.includes("I:") || value.includes("I :")) {
            const values = value.split("\n");
            const filter = values.filter(x => (x.includes("I:") || x.includes("I :")) && (x.includes("FC") || x.includes("LD")))[0];
            if(filter && filter.trim().length > 0) {
                result = filter.split(":")[1].trim().split(" ")[0].trim();
            }
        }
    }
    return result;
}

function calendaire(item) {
    const calendarCodes = [
        "YE", "MO", "DY"
    ];
    const result = {
        code: "",
        value: ""
    };
    const value = item["THRESHOLD/INTERVAL/SAMPLE"].replaceAll(/\r/g, "");
    if(value) {
        if(value.includes("I:") || value.includes("I :")) {
            const values = value.split("\n");
            const filter = values.filter(x => (x.includes("I:") || x.includes("I :")) &&
            (calendarCodes.map(c => x.includes(c)).filter(f => f == true).length > 0))[0];
            if(filter && filter.trim().length > 0) {
                if(filter.split(":")[1].trim().split(" ").length == 1) {
                    result.code = filter.split(":")[1].trim().split(" ")[0].trim();
                    result.value = "1";
                } else {
                    result.code = filter.split(":")[1].trim().split(" ")[1].trim();
                    result.value = filter.split(":")[1].trim().split(" ")[0].trim();
                }
            }
        }
    }
    const sourceValue = item["SOURCES"].replaceAll(/\r/g, "");
    if(sourceValue) {
        if(sourceValue.includes("LUR")) {
            const lurValue = sourceValue.trim().split("\n").filter(x => x.includes("LUR"));
            let lurCalValue;
            if(lurValue.length > 0) {
                lurCalValue = lurValue[0].replaceAll(/\(|\)/g, "").split(":")[1].split("=")[1].split(" ")[0].trim();
            }
            ["YE", "MO", "DY"].forEach(x => {
                if(lurCalValue.includes(x)) {
                    result.code = x;
                    result.value = lurCalValue.replace(x, "");
                }
            })
        }
    }
    return result;
}

function codeCheck(item) {
    const tcc = item["EQUIPMENT"] && item["EQUIPMENT"].replaceAll(/\r|\n/g, "").trim().length > 0 ? true : false;
    if(tcc) return "TCC";

    let result = "";
    const value =  item["THRESHOLD/INTERVAL/SAMPLE"].replaceAll(/\r/g, "");
    const sourceValue = item["SOURCES"].replaceAll(/\r/g, "");
    const checkCodes = [
        " A", " C", " WY"
    ];

    if(sourceValue) {
        if(sourceValue.includes("LUR")) {
            const lurValue = sourceValue.trim().split("\n").filter(x => x.includes("LUR"));
            if(lurValue.length > 0 && lurValue[0].includes("or NR")) {
                return "to be filled";
            }
        }
    }

    if(value) {
        if(value.includes("I:") || value.includes("I :")) {
            const values = value.split("\n");
            let filter = values.filter(x => (x.includes("I:") || x.includes("I :")) && x.includes("NR"))[0];
            if(filter && filter.trim().length > 0) {
                return "to be filled";
            }

            filter = values.filter(x => (x.includes("I:") || x.includes("I :")) &&
            (checkCodes.map(c => x.includes(c)).filter(f => f == true).length > 0));
            if(filter.length == 1) {
                result = filter[0].split(":")[1].replaceAll(/\s/g, "").trim();
            } else if(filter.length > 1) {
                result = "to be filled";
            }
        }
    }
    return result;
}

function acZone(item) {
    const element = item["ZONE"];
    if(element === undefined) return "";
    const elements = element.replaceAll(/\r/g, "").split("\n").filter(x => x.trim().length !== 0);
    while(elements.length < 22) elements.push("");
    return elements;
}

function acces(item) {
    const element = item["ACCESS"];
    if(element === undefined) return "";
    const elements = element.replaceAll(/\r/g, "").split("\n").filter(x => x.trim().length !== 0);
    while(elements.length < 50) elements.push("");
    return elements;
}

function ressource(item) {
    const element = item["SKILL"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "").trim();
}

function chargeHh(item) {
    const element = item["MH/ZONE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r/g, "").split("\n")[0].trim();
}

function nombre(item) {
    const element = item["MAN"];
    if(element === undefined) return "";
    return element.replaceAll(/\r/g, "").split("\n")[0].trim();
}

function libelleCarte(item) {
    let element = item["TITLE"];
    let isCut = false;
    if(element === undefined) return ["", isCut];
    element =  element.replaceAll(/\r/g, "").replaceAll(/\n/g, " ").trim();
    while(element.length > 40) {
        isCut = true;
        const splitedElement = element.split(" ");
        element = splitedElement.slice(0, splitedElement.length - 1).join(" ");
    }
    return [element, isCut];
}

function arrayTextCreation(text) {
    const result = text.split(" ").reduce((r, x) => {
        if((r[r.length - 1] + " " + x).trim().length < 76) {
            return [...r.slice(0, r.length - 1), (r[r.length - 1] + " " + x).trim()];
        } else {
            return [...r, x];
        }
    }, [""]);
    return result;
}

function textSplited75(arr) {
    const result = [];
    arr.forEach(x => {
        if(x.trim().length <= 75) result.push(x.trim());
        else {
            const words = x.trim().split(" ").filter(y => y.length !== 0);
            let element = "";
            words.forEach((w, i) => {
                if((element.length === 0 ? 0 : element.length + 1) + w.length <= 75) {
                    element += (element.length === 0 ? "" : " ") + w;
                } else {
                    result.push(element);
                    element = w;
                }
                if(element.length !== 0 && (i+1) === words.length) {
                    result.push(element);
                }
            })
        }
    })
    return result;
}

function texteLigne(item) {
    let elementDescription = item["DESCRIPTION"];
    let elementPreparation = item["PREPARATION DESCRIPTION"];
    let elementEfectivity = item["EFFECTIVITY"];

    if(elementDescription === undefined) {
        const result = [];
        while(result.length < 16) result.push("");
        return result;
    };

    elementDescription = elementDescription.replaceAll(/\r/g, "").trim();
    elementPreparation = elementPreparation.replaceAll(/\r/g, "").trim();
    elementEfectivity = elementEfectivity.replaceAll(/\r/g, "").trim();

    let splitedDescription = textSplited75(elementDescription.split("\n").filter(x => x.length != 0));
    let splitedPreparation = textSplited75(elementPreparation.split("\n").filter(x => x.length != 0));
    let splitedEfectivity = textSplited75(elementEfectivity.split("\n").filter(x => x.length != 0));

    if((splitedDescription.length + splitedPreparation.length + splitedEfectivity.length) <= 14) {
        let result =  [
            ...splitedDescription,
            splitedPreparation != [""] && splitedPreparation.length !== 0 ? "PREPARATION:" : "",
            ...splitedPreparation,
            splitedEfectivity != [""] && splitedEfectivity.length !== 0 ? "EFFECTIVITY:" : "",
            ...splitedEfectivity
        ].filter(x => x.length !== 0);

        while(result.length < 16) {
            result.push("");
        }

        return result;
    }

    elementDescription = elementDescription.replaceAll(/\r/g, "").replaceAll(/\n/g, " ").trim();
    elementPreparation = elementPreparation.replaceAll(/\r/g, "").replaceAll(/\n/g, " ").trim();
    elementEfectivity = elementEfectivity.replaceAll(/\r/g, "").replaceAll(/\n/g, " ").trim();

    splitedDescription = arrayTextCreation(elementDescription);
    splitedPreparation = arrayTextCreation(elementPreparation);
    splitedEfectivity = arrayTextCreation(elementEfectivity);

    if((splitedDescription.length + splitedPreparation.length + splitedEfectivity.length) <= 14) {
        let result =  [
            ...splitedDescription,
            splitedPreparation != [""] ? "PREPARATION:" : "",
            ...splitedPreparation,
            splitedEfectivity != [""] ? "EFFECTIVITY:" : "",
            ...splitedEfectivity
        ].filter(x => x.length !== 0);

        while(result.length < 16) {
            result.push("");
        }

        return result;
    }

    splitedPreparation = arrayTextCreation("PREPARATION: " + elementPreparation);
    splitedEfectivity = arrayTextCreation("EFFECTIVITY: " + elementEfectivity);

    if((splitedDescription.length + splitedPreparation.length + splitedEfectivity.length) <= 16) {
        let result =  [
            ...splitedDescription,
            ...splitedPreparation,
            ...splitedEfectivity
        ];

        while(result.length < 16) {
            result.push("");
        }

        return result;
    }

    const wholeText =  elementDescription + " PREPARATION: " + elementPreparation + " EFFECTIVITY: " + elementEfectivity;
    const splitedText = arrayTextCreation(wholeText);

    if(splitedText.length === 16) {
        return splitedText;
    }

    // if(splitedText.length > 16) {
    //     return splitedText.slice(0, 16);
    // }
    
    while(splitedText.length < 16) {
        splitedText.push("");
    }

    return splitedText;
}

function ata(item) {
    const element = item["TASK REFERENCE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "").trim().slice(0,2);
}

function checkForJobProcedureAndType(source, type, result, refTask) {
    source.forEach(x => {
        if(x.includes(type)) {
            // if(!["MP", "ESP"].includes(type)) console.log(x.trim().split(type + " :"))//.filter(x => x.length != 0)[0].replaceAll(/\(|\)/g, "").trim());
            result.push({
                reference: ["MP", "ESP"].includes(type) ?
                    x.trim().split(type + ":").filter(x => x.length != 0)[0].trim() :
                    ["MPD","CPCP"].includes(type) ? ("MPD" == type ? refTask : "") :
                        x.replaceAll(/\(|\)/g, "").trim().split(type + " :").filter(x => x.length != 0)[0].trim(),
                type: type
            });
        };
    });
}

function referenceDocumentLie(item) {
    const elementJobProcedure = item["JOB PROCEDURE"];
    const elementSources = item["SOURCES"];
    let elementTaskRef = item["TASK REFERENCE"];
    
    if(elementSources === undefined) return [
        {
            reference: "",
            type: "",
        },
        {
            reference: "",
            type: "",
        },
        {
            reference: "",
            type: "",
        },
        {
            reference: "",
            type: "",
        },
        {
            reference: "",
            type: "",
        },
    ];

    if(elementTaskRef) {
        elementTaskRef = elementTaskRef.replaceAll(/\r|\n|\s/g, "");
    }

    const result = [];
    const splitedJobProcedure = elementJobProcedure.replaceAll(/\r/g, "").split("\n").filter(x => x.length !== 0);
    const splitedSources = elementSources.replaceAll(/\r/g, "").split("\n").filter(x => x.length !== 0);

    ["MP", "ESP"].forEach(x => {
        checkForJobProcedureAndType(splitedJobProcedure, x, result, elementTaskRef);
    });

    ["CMR", "AWL", "AD", "MRBR", "LUR", "CPCP", "CDCCL", "MPD"].forEach(x => {
        checkForJobProcedureAndType(splitedSources, x, result, elementTaskRef);
    });

    splitedJobProcedure.forEach(x => {
        const splited = x.trim().split(":");
        const type = splited[0].trim();
        const reference = splited[1].trim();
        if(!["MP", "ESP"].includes(type)) {
            result.push({
                reference: reference,
                type: type,
            });
        };
    });

    while(result.length < 5) {
        result.push({
            reference: "",
            type: "",
        });
    }

    return result;
}

function typeModifiation(item) {
    const element = item["REV CODE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "");
}

function comments(acZone, acces, libelleCarte, textLigne, refDocLie) {
    let result = "";
    if(acZone.length > 22) {
        result += "(more than 22 zones)";
    }
    if(acces.length > 50) {
        result += "(more than 50 acces)";
    }
    if(libelleCarte[1]) {
        result += "(title with more than 50 characters)";
    }
    if(textLigne.length > 16) {
        result += "(more than 16 cells for text)";
    }
    if(refDocLie.length > 5) {
        result += "(more than 5 documents linked)";
    }
    return result;
}


function transformation(arr, staticValuesDict) {
    const transformedArr = [];

    arr.forEach(x => {
        const s1 = seuil1(x);
        const s2 = seuil2(x);
        const s3 = seuil3(x);
        const cal = calendaire(x);
        const cCheck = codeCheck(x);
        const zone = acZone(x);
        const acc = acces(x);
        const libCarte = libelleCarte(x);
        const txtLigne = texteLigne(x);
        const refDocLie = referenceDocumentLie(x);
        // console.log(
        //     // x,
        // );
        const result = {
            "Rfrence de la Carte": referenceCarte(x),
            "Type de la Carte": typeCarte(x),
            "Spcialit": specialite(x),
            "Contrle": controle(x),
            "Report": report(x),
            "Out of Phase": outOfPhase(cCheck),
            "Phase" : "",
            "Poste" : "",
            "Catgorie MRB": categorieMRB(x),
            "Code Tache" : codeTache(x),
            "Note" : "",
            "Texte de la Note 1" : "",
            "Texte de la Note 2" : "",
            "Texte de la Note 3" : "",
            "Code Seuil 1": s1.code,
            "Seuil 1": s1.value,
            "Code Seuil 2": s2.code,
            "Seuil 2": s2.value,
            "Code Seuil 3": s3.code,
            "Seuil 3": s3.value,
            "Heures": heures(x),
            "Cycles": cycles(x),
            "Code Cal.": cal.code,
            "Calendaire": cal.value,
            "Code Check": cCheck,
            "Code Atypique 1": "",
            "Intervalle Atypique 1": "",
            "Code Atypique 2": "",
            "Intervalle Atypique 2": "",
            "Code Atypique 3": "",
            "Intervalle Atypique 3": "",
            "Tolrance - Tolrance": "",
            "Tolrance - Name": "",
            "Tolrance - Heures": "",
            "Tolrance - Cycles": "",
            "Tolrance - Calendaire": "",
            "Tolrance - Code Calendaire": "",
            "Tolrance - CPT1": "",
            "Tolrance - CPT2": "",
            "Tolrance - CPT3": "",
            "Mthode de Calcul calendaire": "",
            "Mthode de Calcul autres": "",
            ...zone.reduce((obj, e, i) => {
                if(Object.keys(obj).length < 22) obj[`A/C ZONE ${i+1}`] = e;
                return obj;
            }, {}),
            ...acc.reduce((obj, e, i) => {
                if(Object.keys(obj).length < 50) obj[`ACCES ${i+1}`] = e;
                return obj;
            }, {}),
            "Famille MSN 1": "",
            "Famille MSN 2": "",
            "Famille MSN 3": "",
            "Famille MSN 4": "",
            "Famille MSN 5": "",
            "Ressource (Qualification) 1": ressource(x),
            "Charge H.h 1": chargeHh(x),
            "Nombre 1": nombre(x),
            "Ressource (Qualification) 2": "",
            "Charge H.h 2": "",
            "Nombre 2": "",
            "Ressource (Qualification) 3": "",
            "Charge H.h 3": "",
            "Nombre 3": "",
            "Libell de la Carte": libCarte[0],
            ...txtLigne.reduce((obj, e, i) => {
                if(Object.keys(obj).length < 16) obj[`Text Ligne ${i+1}`] = e;
                return obj;
            }, {}),
            "ATA": ata(x),
            "Libell de l'ATA (optionnel)": "",
            "Rfrence document li 1": refDocLie[0].reference,
            "Type de Document 1": refDocLie[0].type,
            "Rfrence document li 2": refDocLie[1].reference,
            "Type de Document 2": refDocLie[1].type,
            "Rfrence document li 3": refDocLie[2].reference,
            "Type de Document 3": refDocLie[2].type,
            "Rfrence document li 4": refDocLie[3].reference,
            "Type de Document 4": refDocLie[3].type,
            "Rfrence document li 5": refDocLie[4].reference,
            "Type de Document 5": refDocLie[4].type,
            "Numro Amendement": staticValuesDict["mpEdition"],
            "Date Amendement": staticValuesDict["mpEditionDate"],
            "Numro Rvision": staticValuesDict["mpdRevisionValue"],
            "Date Rvision": staticValuesDict["mpdRevisionDate"],
            "Type de modification": typeModifiation(x),
            "Comments": comments(zone, acc, libCarte, txtLigne, refDocLie),
        };

        transformedArr.push(result);
    });

    return transformedArr;
}

export {
    transformation
}