<template>
  <div class="d-flex flex-column">
    <div class="inputs-container">
      <div class="input-element">
        <label for="mp-edition">
          MP edition:
        </label>
        <input id="mp-edition" type="text" v-model="mpEdition">
        <span class="icon icon-16 icon-info" data-toggle="tooltip" data-placement="right" title="'Numro Amendement' value for AMASIS excel column"></span>
      </div>
      <div class="input-element">
        <label for="mp-edition-date">
          MP edition date:
        </label>
        <input id="mp-edition-date" type="text" v-model="mpEditionDate">
        <span class="icon icon-16 icon-info" data-toggle="tooltip" data-placement="right" title="'Date Amendement' value for AMASIS excel column"></span>
      </div>
      <div class="input-element">
        <label for="mpd-revision-value">
          MPD revision value:
        </label>
        <input id="mpd-revision-value" type="text" v-model="mpdRevisionValue">
        <span class="icon icon-16 icon-info" data-toggle="tooltip" data-placement="right" title="'Numro Rvision' value for AMASIS excel column"></span>
      </div>
      <div class="input-element">
        <label for="mpd-revision-date">
          MPD revision date:
        </label>
        <input id="mpd-revision-date" type="text" v-model="mpdRevisionDate">
        <span class="icon icon-16 icon-info" data-toggle="tooltip" data-placement="right" title="'Date Rvision' value for AMASIS excel column"></span>
      </div>
    </div>
    <input id="file-input-atr" class="form-control mt-4" type="file" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" @change="readFile">
    <button class="btn btn-primary mt-3" :class="[this.allInfoReady ? 'disabled' : '']" @click="transformFile">TRANSFORM</button>
    <a class="btn mt-3" :class="[this.download_workbook === '' ? 'disabled btn-secondary' : 'btn-success']" @click="downloadFile">DOWNLOAD</a>
  </div>
</template>

<script>
import { transformation } from "@/js-modules/functionsATR";
const XLSX = require("xlsx");

export default {
  name: 'MPDTransformationATR',
  data() {
    return {
      workbook: "",
      download_workbook: "",

      mpEdition: "",
      mpEditionDate: "",
      mpdRevisionValue: "",
      mpdRevisionDate: "",
    }
  },
  computed: {
    allInfoReady() {
      return (this.workbook === '' || this.mpEdition === '' || this.mpEditionDate === '' || this.mpdRevisionValue === '' || this.mpdRevisionDate === '');
    },
    allVariablesAffectingDownload() {
      return [
        this.workbook,
        this.mpEdition,
        this.mpEditionDate,
        this.mpdRevisionValue,
        this.mpdRevisionDate
      ]
    }
  },
  methods: {
    readFile() {
      const input = document.querySelector("#file-input-atr");
      this.file = input.files[0];
      const reader = new FileReader();

      reader.onload = (e) => {
        /* Parse data */
        const bstr = e.target.result;
        const wb = XLSX.read(bstr, { type: 'binary' });
        // console.log(wb);
        this.workbook = wb;
      }

      // console.log(reader.readAsBinaryString(this.file));
      return reader.readAsBinaryString(this.file);
    },
    transformFile() {
      this.transformFileNormal()
    },
    transformFileNormal() {
      const MPD_sheet = this.workbook.Sheets["MPDTasks"];

      const arr = XLSX.utils.sheet_to_json(MPD_sheet,
          {header: [
              "REV CODE",
              "TASK REFERENCE",
              "TITLE",
              "DESCRIPTION",
              "SECTION",
              "THRESHOLD/INTERVAL/SAMPLE",
              "SOURCES",
              "EFFECTIVITY",
              "JOB PROCEDURE",
              "ZONE",
              "MH/ZONE",
              "MAN",
              "ACCESS",
              "MH/ACCESS",
              "PREPARATION DESCRIPTION",
              "MH/PREPARATION",
              "SKILL",
              "EQUIPMENT"
          ]
      });

      let slicedArr = arr.slice(2);
      const staticValuesDict = {
        mpEdition: this.mpEdition,
        mpEditionDate: this.mpEditionDate,
        mpdRevisionValue: this.mpdRevisionValue,
        mpdRevisionDate: this.mpdRevisionDate,
      };
      const transformedArr = transformation(slicedArr, staticValuesDict);
      // console.log(transformedArr);
      const download_worksheet = XLSX.utils.json_to_sheet(transformedArr);

      this.download_workbook = XLSX.utils.book_new();
      this.download_workbook.SheetNames.push("MPD induction AMASIS");
      this.download_workbook.Sheets["MPD induction AMASIS"] = download_worksheet;
    },
    downloadFile() {
      let name = "file";
      XLSX.writeFile(this.download_workbook, `${name}.xlsx`);
    }
  },
  watch: {
    // optimized() {
    //   this.download_workbook = "";
    // },
    // workbook() {
    //   this.download_workbook = "";
    // },
    allVariablesAffectingDownload() {
      this.download_workbook = "";
    }
  },
  mounted() {
    // const a = "LUR: 4 C"
    // const splitedA = a.trim().split("\n").filter(x => x.includes("LUR"))[0].split("LUR: ").filter(x => x.length > 0)[0].split(" ").filter(x => x.length > 0)[0];
    // console.log(isNaN(Number("4")));
  },
}
</script>

